<template>
  <div class="business-portal-page">
    <!-- title -->
    <portal-header :projectObj="titleConfig"></portal-header>
    <!-- advance search -->
    <portal-form
      ref="bankAccountForm"
      :form-data="searchForm"
      :form-rules="searchFormRules"
      :form-config-list="formConfigArray"
      :searchBtnLoading="searchLoading"
      @reset-btn-click="reset"
      @search-btn-click="search"
    />
    <div class="table-list-container">
      <el-tabs v-model="activeName">
        <el-tab-pane :label="$t('IDC.bankAccount.label.activeAccount')" name="first">
          <transition name="el-fade-in-linear">
            <active-account-content
              ref="activeAccountContent"
              v-show="activeName === 'first'"
              :identity-id="identity"
              :searchForm="searchForm"
              :assetTypeOptions="assetTypeOptions"
              :bankHeadOfficeOptions="bankHeadOfficeOptions"
              @refreshRemovedAccount="refreshRemovedAccountList"
            ></active-account-content>
          </transition>
        </el-tab-pane>
        <el-tab-pane :label="$t('IDC.bankAccount.label.removedAccount')" name="second">
          <transition name="el-fade-in-linear">
            <removed-account-content
              ref="removedAccountContent"
              v-show="activeName === 'second'"
              :identity-id="identity"
              :searchForm="searchForm"
              :assetTypeOptions="assetTypeOptions"
              :bankHeadOfficeOptions="bankHeadOfficeOptions"
            ></removed-account-content>
          </transition>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import { queryDict, getBankHeadOfficeList } from '@/views/identifier-center/api/api-bank-account.js';
import { nonSpecialCharValidateObj } from '@/common/validator/common';
import PortalHeader from '@/components/portal-header';
import PortalForm from '@/components/portal-form';
import ActiveAccountContent from '@/views/identifier-center/pages/bank-account/components/active-account-content';
import RemovedAccountContent from '@/views/identifier-center/pages/bank-account/components/removed-account-content';

export default {
  name: 'BankAccountList',
  components: {
    PortalHeader,
    PortalForm,
    ActiveAccountContent,
    RemovedAccountContent,
  },
  props: {
    identityId: {
      require: true,
      type: String,
    },
  },
  data() {
    return {
      identity: '',
      searchForm: {
        assetType: null,
        accountName: null,
        bankHeadOffice: null,
        bankName: null,
      },
      searchFormRules: {
        accountName: [nonSpecialCharValidateObj],
        bankName: [nonSpecialCharValidateObj],
      },
      bankHeadOfficeOptions: [
        {
          value: null,
          label: 'All',
        },
      ],
      assetTypeOptions: [
        {
          value: null,
          label: 'All',
        },
      ],
      searchLoading: false,
      activeName: 'first',
    };
  },
  computed: {
    titleConfig() {
      return {
        title: this.$t('IDC.bankAccount.pageTitle'),
        desc: this.$t('IDC.bankAccount.pageDesc'),
      };
    },

    formConfigArray() {
      return [
        {
          label: this.$t('IDC.bankAccount.label.assetType'),
          key: 'assetType',
          comp: 'el-select',
          options: this.assetTypeOptions,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            id: 'assetType',
          },
        },
        {
          label: this.$t('IDC.bankAccount.label.accountName'),
          key: 'accountName',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            id: 'accountName',
          },
        },
        {
          label: this.$t('IDC.bankAccount.label.bankHeadOffice'),
          key: 'bankHeadOffice',
          comp: 'el-select',
          options: this.bankHeadOfficeOptions,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            id: 'bankHeadOffice',
          },
        },
        {
          label: this.$t('IDC.bankAccount.label.bank'),
          key: 'bankName',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            id: 'bankName',
          },
        },
      ];
    },
  },
  watch: {},
  created() {
    this.getAssetTypeList();
    this.getBankHeadOfficeList();
    if (!this.identityId) {
      const { identityId } = JSON.parse(decodeURIComponent(this.$route.query.data));
      this.identity = identityId;
    } else {
      this.identity = this.identityId;
    }
  },
  activated() {},
  mounted() {},
  methods: {
    reset() {
      this.searchForm = { bankHeadOffice: null, assetType: null };
      this.$refs.activeAccountContent.search();
      this.$refs.removedAccountContent.search();
    },
    search() {
      this.$refs.activeAccountContent.search();
      this.$refs.removedAccountContent.search();
    },
    async getBankHeadOfficeList() {
      let res = await getBankHeadOfficeList({});
      if (res.header && res.header.success) {
        let dataDictJson = JSON.parse(res.jsonDataStr);
        dataDictJson.map((item) => {
          let officeObj = {
            value: item.code,
            label: item.value,
          };
          this.bankHeadOfficeOptions.push(officeObj);
        });
      }
    },
    async getAssetTypeList() {
      let res = await queryDict({ dictType: 'AssetType' });
      if (res.header && res.header.success) {
        let dataDictJson = JSON.parse(res.dataDictJson);
        dataDictJson.map((item) => {
          let assetTypeObj = {
            value: item.code,
            label: item.name,
          };
          this.assetTypeOptions.push(assetTypeObj);
        });
      }
    },
    refreshRemovedAccountList() {
      this.$refs.removedAccountContent.search();
    },
  },
};
</script>

