<template>
  <div class="review-transaction">
    <div>
      <portal-header :projectObj="titleConfig" />
      <portal-form
        ref="balanceForm"
        :form-data="searchList"
        :form-config-list="formConfigList"
        :needOperateBtn="true"
        :needBottomLine="false"
        :searchBtnLoading="searchLoading"
        @reset-btn-click="reset"
        @search-btn-click="search"
      />
    </div>
    <div class="table-list-container">
      <div class="divider-line"></div>
      <div style="margin: 20px 0">
        <el-dropdown @command="exportData">
          <el-button type="primary" size="small" :disabled="isExportDisabled">
            {{ $t('common.button.export') }}
            <i class="el-icon-arrow-down el-icon--right" style="font-size: 12px"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="CSV">CSV</el-dropdown-item>
            <el-dropdown-item command="PDF">PDF</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>

      <table-page
        size="small"
        class="table-page"
        :has-pagination="true"
        :columns="columns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :is-search="refreshTableFlag"
      >
        <template slot="organizationName" slot-scope="scope">
          <el-link :underline="true" @click="viewDetail(scope.data.row)">{{ scope.data.row.organizationName }}</el-link>
        </template>
        <template slot="parentOrganizationName" slot-scope="scope">
          <el-link :underline="true" @click="viewDetail(scope.data.row, true)">{{
            scope.data.row.parentOrgName
          }}</el-link>
        </template>
      </table-page>
    </div>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage';
import PortalHeader from '@/components/portal-header/index.vue';
import PortalForm from '@/components/portal-form';
import {
  downloadBalanceOverview,
  getAccountType,
  queryBalanceOverviewPage,
} from '@/views/identifier-center/api/api-organization';

import { exportUtils } from '@/common/utils/export-utils';
import { dealApiRes, transOptions } from '@/views/identifier-center/pages/organization/utils/common';
import AmountUnit from '@/views/business-center/pages/settlement/utils/amount-utils.js';
export default {
  name: 'BalanceOverview',
  components: { TablePage, PortalHeader, PortalForm },
  props: {
    propShortCode: {
      type: String,
      requied: true,
    },
  },
  data() {
    return {
      searchLoading: false,
      refreshTableFlag: false,
      isExportDisabled: true,
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      searchList: {
        shortCode: '',
        accountType: '',
      },
      accountTypeList: [],
      columns: [
        { label: 'Level', prop: 'level', align: 'left', width: 100 },
        {
          label: 'Organization Name',
          prop: 'organizationName',
          align: 'left',
          __slotName: 'organizationName',
          width: 200,
        },
        {
          label: 'Parent Organization Name',
          prop: 'parentOrgName',
          align: 'left',
          __slotName: 'parentOrganizationName',
          width: 250,
        },
        { label: 'Account Type Alias', prop: 'accountAlias', align: 'left', width: 200 },
        {
          label: 'Current Balance',
          prop: 'currentBalanceSymbol',
          align: 'right',
          width: 200,
        },
        {
          label: 'Available Balance',
          prop: 'availableBalanceSymbol',
          align: 'right',
          width: 200,
        },
        {
          label: 'Reserved Balance',
          prop: 'reservedBalanceSymbol',
          align: 'right',
          width: 200,
        },
        {
          label: 'Unclear Balance',
          prop: 'unclearBalanceSymbol',
          align: 'right',
          width: 200,
        },
      ],

      queryFormRules: {
        time: [{ required: true, message: 'Please select time', trigger: 'change' }],
      },
    };
  },
  created() {
    this.init();
  },
  computed: {
    titleConfig() {
      return {
        title: 'Balance Overview',
        desc: 'You can query and export account balance information about the current organization and its child organizations.',
      };
    },
    formConfigList() {
      return [
        {
          label: 'Organization Short Code',
          key: 'shortCode',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          label: 'Account Type',
          key: 'accountType',
          comp: 'el-select',
          options: this.accountTypeList,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
          hidden: false,
        },
      ];
    },
  },
  methods: {
    async init() {
      await this.initAccountTypeList();
    },
    async initAccountTypeList() {
      let list = await dealApiRes(getAccountType, 'dics');
      list = list || [];
      list = list.map(transOptions('code', 'name'));
      this.accountTypeList = list;
    },
    reset() {
      this.$refs.balanceForm.resetFields();
      this.paginationConfig = {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      };
      this.refreshTableFlag = true;
      this.isExportDisabled = true;
    },
    search() {
      this.$refs['balanceForm'].validate((isValid) => {
        if (isValid) {
          this.refreshTableFlag = true;
          this.paginationConfig = {
            total: 0,
            pageIndex: 1,
            pageSize: 10,
          };
        }
      });
    },

    exportData(fileType) {
      let shortCode = this.searchList.shortCode;
      let queryChildren = false;
      if (!shortCode) {
        shortCode = this.propShortCode;
        queryChildren = true;
      }
      let paramObj = {
        shortCode,
        queryChildren,
        accountTypeId: this.searchList.accountType,
        pageSize: this.paginationConfig.total,
        pageNo: 1, //  1,
        fileType: fileType,
      };

      exportUtils(downloadBalanceOverview, paramObj, { fileType });
    },

    getTableData() {
      let shortCode = this.searchList.shortCode;
      let queryChildren = false;
      if (!shortCode) {
        shortCode = this.propShortCode;
        queryChildren = true;
      }
      let param = {
        shortCode,
        queryChildren,
        accountTypeId: this.searchList.accountType,
        pageSize: this.paginationConfig.pageSize,
        pageNo: this.paginationConfig.pageIndex,
      };
      return queryBalanceOverviewPage(param)
        .then((res) => {
          res.list =
            res.accounts.map((item) => ({
              ...item,
              ...JSON.parse(sessionStorage.getItem(this.$route.params)),
            })) || [];
          res.list.forEach((item) => {
            item.currentBalanceSymbol = this.formatBalance(item.accountBalance, 'currentBalance');
            item.availableBalanceSymbol = this.formatBalance(item.accountBalance, 'availableBalance');
            item.reservedBalanceSymbol = this.formatBalance(item.accountBalance, 'reservedBalance');
            item.unclearBalanceSymbol = this.formatBalance(item.accountBalance, 'unclearBalance');
          });
          if (res.list.length) {
            this.isExportDisabled = false;
          } else {
            this.isExportDisabled = true;
          }
          if (!res.header) {
            res.header = {
              success: false,
            };
            res.total = 0;
          }
          res.totalRecords = res.total;
          return res;
        })
        .catch(() => {
          this.isExportDisabled = true;
          const res = {
            header: {},
            list: [],
          };
          return res;
        })
        .finally(() => {
          this.refreshTableFlag = false;
        });
    },
    formatBalance(obj, key) {
      return obj[key] ? AmountUnit.format(obj[key].amount / 100, obj[key].currencyCode) : '-';
    },
    viewDetail(row, parent) {
      let id = row.identityId;
      let propShortCode = row.shortCode;
      if (parent) {
        id = row.parentOrgId;
        propShortCode = row.parentShortCode;
      }
      if (id && propShortCode) {
        const params = {
          identityId: id,
          organizationId: id,
          propShortCode,
        };
        this.openNewWindow({ path: 'detail', query: { data: encodeURIComponent(JSON.stringify(params)) } });
      }
    },
    showBalanceWithUnit(data) {
      if (!data.amount) {
        data.amount = 0;
      }
      return data.currencyCode === 'USD'
        ? `$${(+data.amount).toFixed(2).replace(/\d{1,3}(?=(\d{3})+(\.\d*)?$)/g, '$&,')}`
        : `${(+data.amount).toFixed(2).replace(/\d{1,3}(?=(\d{3})+(\.\d*)?$)/g, '$&,')} ${data.currencyCode}`;
    },
  },
};
</script>

