import {
  inputRequiredValidateObj,
  len19StrValidateObj,
  len24StrValidateObj,
  len4To12StrValidateObj,
  len64StrValidateObj,
  len6To16StrValidateObj,
  len256StrValidateObj,
  nonSpecialCharExceptPercentSignValidateObj,
  selectRequiredValidateObj,
} from '@/common/validator/common';
import { checkMerchantName, checkShortCode, getOrganizationList } from '@/views/identifier-center/api/api-organization';
import dayjs from 'dayjs';

export const DATE_FORMAT = {
  COMP_DISPLAY: 'dd/MM/yyyy HH:mm',
  DAYJS_DISPLAY: 'DD/MM/YYYY HH:mm',
  COMP_VALUE: 'yyyy-MM-dd HH:mm',
  DAYJS: 'YYYY-MM-DD HH:mm',
};

const phoneNumberRuleObj = {
  pattern: /^((13[5-9])|(1[4-9][0-9]))[0-9]{8}$/,
  message: 'Please Enter a correct IMSI.',
  trigger: 'blur',
};

const emailRuleObj = {
  type: 'email',
  message: 'Please enter only letters(a-z),numbers(0-9),periods(.),and at(@).',
  trigger: 'blur',
};
const catchQueryCodeError = (callback) => callback(new Error('Query short code exception.'));

let getSearchOrgRules = () => {
  return {
    shortCode: [nonSpecialCharExceptPercentSignValidateObj, len24StrValidateObj],
    identityId: [nonSpecialCharExceptPercentSignValidateObj, len19StrValidateObj],
  };
};
// selectRequiredValidateObj + len4To12StrValidateObj
let parentShortCodeChartValidateObj = (identityInfo, assignInfo) => {
  return {
    required: true,
    trigger: ['blur', 'change'],
    validator(rule, value, callback) {
      let msg = '';
      if (!value) {
        msg = 'Please select content';
        identityInfo.parentOrgName = '';
        return callback(new Error(msg));
      }
      const conLen = value.length;
      const minLen = 4;
      const maxLen = 12;

      if (conLen >= 4 && conLen <= maxLen) {
        return callback();
      }
      msg = `Must be between ${minLen} and ${maxLen} characters`;
      identityInfo.parentOrgName = '';
      callback(new Error(msg));
    },
  };
};

let getIdentityInfoRules = (identityInfo, assignInfo) => {
  return {
    parentShortCode: [
      parentShortCodeChartValidateObj(identityInfo, assignInfo),
      checkParentShortCodeExist(identityInfo, assignInfo),
      { trigger: 'change' },
    ],
    shortCode: [inputRequiredValidateObj, len4To12StrValidateObj, checkShortCodeExist()],
    merchName: [inputRequiredValidateObj, checkRegex(identityInfo.orgNameRegex), checkOrgNameExist()],
    notificationChannel: [selectRequiredValidateObj],
    phoneNumber: [phoneNumberRuleObj, len64StrValidateObj, checkPhoneNumber(identityInfo)],
    email: [emailRuleObj, len64StrValidateObj, checkEmail(identityInfo)],
    language: [selectRequiredValidateObj],
    selectedProdList: [checkProductObj(identityInfo)],
  };
};

const checkProductObj = (identityInfo) => {
  const productVlidator = (rule, value, callback) => {
    const product = identityInfo.selectedProdList;
    if (!product || !product.length) {
      return callback(new Error('Please select one product.'));
    }
    callback();
  };
  return {
    validator: productVlidator,
  };
};

export let checkShortCodeExist = () => {
  const validatorFn = (rule, value, callback) => {
    checkShortCode(value)
      .then((res) => {
        if (!res.header || !res.header.success) {
          callback(new Error('Query short code error.'));
          return;
        }
        if (res.check === 'Invalid') {
          callback(new Error('The Organization short code is invalid.'));
        } else if (res.check === 'Exist') {
          callback(new Error('The Organization Short Code Already Exsists.'));
        } else {
          callback();
        }
      })
      .catch((err) => catchQueryCodeError(callback));
  };
  return {
    validator: validatorFn,
    trigger: 'blur',
  };
};

export let checkShortCodeInvalid = () => {
  const validatorCodeFn = (rule, value, callback) => {
    if (!value) {
      return callback();
    }
    checkShortCode(value)
      .then((res) => {
        if (!res.header || !res.header.success) {
          callback(new Error('Query short code error.'));
          return;
        }

        if (res.check === 'Invalid') {
          callback(new Error('The Organization short code is invalid.'));
        } else {
          callback();
        }
      })
      .catch((err) => catchQueryCodeError(callback));
  };
  return {
    validator: validatorCodeFn,
    trigger: 'blur',
  };
};

let checkParentShortCodeExist = (identityInfo, assignInfo) => {
  const validator = (rule, value, callback) => {
    checkShortCode(value)
      .then((res) => {
        if (!res.header || !res.header.success) {
          identityInfo.parentOrgName = '';
          callback(new Error('Query short code error.'));
          return;
        }

        if (res.check === 'Exist') {
          settingAssignInfo(identityInfo, assignInfo, callback);
        } else if (res.check === 'Invalid') {
          identityInfo.parentOrgName = '';
          callback(new Error('The short code is invalid.'));
        } else {
          identityInfo.parentOrgName = '';
          callback(new Error('The short code is not existed.'));
        }
      })
      .catch((err) => catchQueryCodeError(callback));
  };
  return {
    validator,
    trigger: 'blur',
  };
};

let settingAssignInfo = (identityInfo, assignInfo, callback) => {
  // Hierarchy
  assignInfo.identityModel = '';
  assignInfo.subAgentExpansionModel = '';

  getOrganizationList({ shortCode: identityInfo.parentShortCode })
    .then((res) => {
      if (res.header && res.header.success && res.organizationInfo) {
        let organizationInfo = res.organizationInfo;
        if (organizationInfo.subAgentExpansionModel === '') {
          identityInfo.parentOrgName = '';
          callback(new Error('The short code does not allow sub-organizations.'));
        } else if (organizationInfo.subAgentExpansionModel === '0003') {
          identityInfo.parentOrgName = '';
          callback(new Error('The short code does not allow sub-organizations.'));
        } else if (organizationInfo.subAgentExpansionModel === '0002') {
          identityInfo.parentId = organizationInfo.organizationId;
          identityInfo.parentOrgName = organizationInfo.organizationName;
          assignInfo.parentIdentityModel = organizationInfo.subAgentExpansionModel;
          callback();
        } else {
          if (
            organizationInfo.subAgentExpansionModel !=
            '000' + identityInfo.selectedProdList[0].productProperties.ORG_IDENTITY_MODEL
          ) {
            callback(new Error('SelectedProduct not match subAgentExpansionModel.'));
          } else {
            identityInfo.parentId = organizationInfo.organizationId;
            identityInfo.parentOrgName = organizationInfo.organizationName;
            assignInfo.parentIdentityModel = organizationInfo.subAgentExpansionModel;
            callback();
          }
        }
      }
    })
    .catch(() => {
      identityInfo.parentOrgName = '';
      callback(new Error('Query organization list exception.'));
    });
};

let checkOrgNameExist = () => {
  const validator = (rule, value, callback) => {
    checkMerchantName({ merchantName: value })
      .then((res) => {
        if (res.header && res.header.success) {
          if (res.check === 'Invalid') {
            callback(new Error('The organization name is invalid.'));
          } else if (res.check === 'Exist') {
            callback(new Error('The organization name is existed.'));
          } else {
            callback();
          }
        } else {
          callback(new Error('Query organization name error.'));
        }
      })
      .catch(() => {
        callback(new Error('Query organization name exception.'));
      });
  };
  return {
    validator,
    trigger: 'blur',
  };
};

let checkRegex = (data) => {
  return {
    validator: (rule, value, callback) => {
      if (!new RegExp(data).test(value)) {
        callback(new Error(data + '.'));
      } else {
        callback();
      }
    },
    trigger: 'blur',
  };
};

let checkPhoneNumber = (scope) => {
  return checkNotification(scope, '1001');
};

let checkEmail = (scope) => {
  return checkNotification(scope, '1011');
};

let checkNotification = (scope, channelCode) => {
  return {
    validator: (rule, value, callback) => {
      if (value === '' && scope.notificationChannel === channelCode) {
        callback(new Error('Please enter content'));
      } else {
        callback();
      }
    },
    trigger: 'blur',
  };
};

const billPayValidationCheckRule = {
  validationExpression: [inputRequiredValidateObj, len256StrValidateObj],
  validationMode: [selectRequiredValidateObj],
  allowPartialPayement: [selectRequiredValidateObj],
};

let getAssignInfoRules = () => {
  return {
    identityModel: [selectRequiredValidateObj],
    subAgentExpansionModel: [selectRequiredValidateObj],
    ruleProfile: [selectRequiredValidateObj],
    aggregatorModel: [selectRequiredValidateObj],
    centralOwnedModel: [selectRequiredValidateObj],
    ...billPayValidationCheckRule,
  };
};

let getSearchOrgOperRules = () => {
  return {
    identityId: [nonSpecialCharExceptPercentSignValidateObj, len19StrValidateObj],
    operatorId: [nonSpecialCharExceptPercentSignValidateObj],
    operatorName: [nonSpecialCharExceptPercentSignValidateObj, len6To16StrValidateObj],
  };
};

//  
const getCurRowProductDateRules = (scope, row, target) => {
  let dateValidator = (rule, value, callback) => {
    //  tool tips
    if (target === 'effectiveTime') {
      row.effectiveErrorMessage = '';
    } else {
      row.expireErrorMessage = '';
    }

    if (!row) {
      return callback();
    }

    const expireErrorMessage = 'The date must be later than  Effective Date.';
    const effectiveErrorMessage = 'The date must be earlier than  Expiry Date.';
    //  
    const effectiveTime = dayjs(row.effectiveTime, DATE_FORMAT.DAYJS);
    const expireTime = dayjs(row.expireTime, DATE_FORMAT.DAYJS);
    const isPassed = effectiveTime < expireTime;

    if (isPassed) {
      return callback();
    }

    if (target === 'effectiveTime') {
      row.effectiveErrorMessage = effectiveErrorMessage;
      return callback(new Error(effectiveErrorMessage));
    }

    if (target === 'expireTime') {
      row.expireErrorMessage = expireErrorMessage;
      return callback(new Error(expireErrorMessage));
    }
    callback(new Error('Invalid time.'));
  };
  return [
    { required: true, message: 'Please enter context' },
    { validator: dateValidator, trigger: 'change' },
  ];
};

const formatStr = (value) => {
  let strArr = (value && value.split('-')) || null;
  return strArr ? `${strArr[1]}/${strArr[0]}/${strArr[2]}` : false;
};

const formOrgChange = () => {
  return {
    distributionId: [selectRequiredValidateObj],
    templateId: [selectRequiredValidateObj],
    description: [inputRequiredValidateObj],
  };
};

export {
  getSearchOrgRules,
  getSearchOrgOperRules,
  getIdentityInfoRules,
  getAssignInfoRules,
  getCurRowProductDateRules,
  formOrgChange,
  formatStr,
};
