<template>
  <el-dialog
    class="portal-dialog"
    :destroy-on-close="true"
    :visible.sync="showDialog"
    :title="conf.title"
    :width="width"
    v-bind="$attrs"
    v-on="$listeners"
    :close-on-click-modal="closeOnClickModal"
    :show-close="showClose"
    :before-close="beforeClose"
    @open="open"
    @close="close"
    @opened="opened"
    @closed="closed"
  >
    <el-form ref="dialog-form" :model="dialogForm" :rules="validationRules">
      <section>
        <div class="label">{{ conf.label }}</div>
        <div class="value">{{ conf.value }}</div>

        <el-form-item
          :label="prop.label"
          v-for="prop in props"
          :key="prop.key"
          :prop="prop.key"
          v-bind="prop.itemBind"
          :class="[prop.validateStatus, prop.itemClass]"
        >
          <el-select
            size="small"
            placeholder="Please select"
            v-if="prop.type === 'select'"
            v-model="dialogForm[prop.key]"
          >
            <el-option v-for="item in prop.options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
          <el-input
            size="small"
            v-model="dialogForm[prop.key]"
            placeholder="Please enter the content"
            v-bind="prop.elementBind"
            :class="prop.eleClass"
            v-else-if="prop.type === 'input'"
          ></el-input>
          <el-input
            v-else-if="prop.type === 'textarea'"
            type="textarea"
            :rows="3"
            v-model="dialogForm[prop.key]"
            placeholder="Please enter"
          ></el-input>
        </el-form-item>
      </section>
      <section>
        <div slot="footer" class="dialog-footer">
          <el-button class="btn-option" size="small" plain @click="cancel">Cancel</el-button>
          <el-button
            class="btn-option"
            size="small"
            type="primary"
            @click="submit(conf.prop)"
            v-loading="submitLoading"
            v-preventReClick
            >Submit</el-button
          >
        </div>
      </section>
    </el-form>
  </el-dialog>
</template>

<script>
import { BasicInfoRule } from '@/views/identifier-center/validator/organization-info';

export default {
  name: 'ContentItemEditDialog',
  props: {
    visible: {
      type: Boolean,
      default: true,
    },
    submitLoading: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: '',
    },
    width: {
      type: String,
      default: '448px',
    },
    closeOnClickModal: {
      type: Boolean,
      default: true,
    },
    showClose: {
      type: Boolean,
      default: true,
    },
    config: {
      type: Object,
      default: () => {},
    },

    data: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      showDialog: false,
      dialogForm: {},
      conf: {},
      props: [],
      text: '',
      validationRules: {},
    };
  },
  watch: {
    visible: {
      handler(value) {
        this.showDialog = value;
      },
      immediate: true,
    },
    config: {
      handler(value) {
        if (value) {
          this.conf = JSON.parse(JSON.stringify(value)) || {};
          if (this.conf.properties && this.conf.properties.length) {
            const props = this.conf.properties;
            const dialogFormConfig = {};
            for (const prop of props) {
              if (prop.useDefult) {
                dialogFormConfig[prop.key] = prop.default;
              }
            }
            this.dialogForm = dialogFormConfig;
            this.props = props;
            this.validationRules = BasicInfoRule(this.props);
            this.$forceUpdate();
          }
        }
      },
      immediate: true,
      deep: true,
    },
    showDialog: {
      handler(value) {
        this.$nextTick(() => {
          this.$refs['dialog-form'] && this.$refs['dialog-form'].clearValidate();
          this.$emit('show-dialog', value);
          this.$emit('update:visible', value);
        });
      },
      immediate: true,
    },
  },
  created() {},
  activated() {},
  methods: {
    beforeClose() {
      this.close();
    },
    open() {},
    opened() {},
    close() {
      this.showDialog = false;
      this.$refs['dialog-form'].resetFields();
    },
    closed() {},
    cancel() {
      this.close();
    },
    submit(prop) {
      this.$refs['dialog-form'].validate((valid) => {
        if (valid) {
          this.$emit('on-submit', prop, this.dialogForm);
        }
      });
    },
  },
};
</script>

