  .el-form-item {
    margin-bottom: 20px;
    &:after {
      content: '';
      margin-top: 24px;
      width: 100%;
      height: 0;
      border-bottom: 1px solid $--fill_color--regular;
    }
    .el-form-item__label {
      line-height: 19px;
      margin-bottom: 8px;
    }
    .el-form-item__content {
      line-height: 19px;
    }
  }
  .el-form-item:last-child {
    &:after {
      content: none;
    }
  }
}
section {
  .label {
    line-height: 19px;
    color: $--text_color--secondary;
  }
  .value {
    margin: 8px 0 20px 0;
    line-height: 19px;
    color: $--text_color--strong;
  }
