import { inputRequiredValidateObj, selectRequiredValidateObj } from '@/common/validator/common';
import { checkOrganizationName } from '@/views/identifier-center/api/api-organization';

const checkOrgNameFun = async (organizationName, callback, properties) => {
  const STATUS = {
    SUCCUSS: 'el-form-item--valiate-succuss',
    PENDING: 'el-form-item--valiate-pending',
    ERROR: 'el-form-item--valiate-error',
  };
  const propItem = properties[0]; // properties

  // message
  const message = 'The organization name is invalid.';

  const REG_FRONT = /^([0-9A-Za-z\\_\\^\\|\\/ ]{2,128})$/; // 
  propItem.validateStatus = STATUS.PENDING;
  if (!REG_FRONT.test(organizationName)) {
    propItem.validateStatus = STATUS.ERROR;
    callback(new Error(message));
    return;
  }
  propItem.validateStatus = STATUS.PENDING;
  try {
    const res = await checkOrganizationName({ organizationName });

    if (res.header && res.header.success && res.flag === true) {
      propItem.validateStatus = STATUS.SUCCUSS;
      callback();
      return;
    }

    if (res.flag === false) {
      let message = res.description;
      propItem.validateStatus = STATUS.ERROR;
      callback(new Error(message));
      return;
    }

    propItem.validateStatus = STATUS.ERROR;
    callback(new Error(message));
  } catch (err) {
    propItem.validateStatus = STATUS.ERROR;
    callback(new Error('Check organization name exception.'));
  }
};

const checkOrgNameValidateObj = (properties) => ({
  trigger: 'blur',
  validator(rule, value, callback) {
    checkOrgNameFun(value, callback, properties);
  },
});

const BasicInfoRule = (properties) => {
  return {
    identityStatus: [selectRequiredValidateObj],
    ruleProfile: [selectRequiredValidateObj],
    remark: [inputRequiredValidateObj],
    newOrganizationName: [inputRequiredValidateObj, checkOrgNameValidateObj(properties)],
  };
};

export { BasicInfoRule };
