<template>
  <el-tooltip :content="value" :disabled="tooltipShow" placement="top-start" :visible-arrow="false">
    <span v-if="!isJump" ref="content" @mouseover="isShowTooltip">{{ value }}</span>
    <el-link @mouseover="isShowTooltip" ref="content" v-else @click="goDetails(value)" type="primary">
      {{ value }}
    </el-link>
  </el-tooltip>
</template>

<script>
export default {
  name: 'DescriptionsTooltip',
  props: {
    value: {
      type: String,
      default: '',
    },
    isJump: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      tooltipShow: false,
    };
  },
  methods: {
    isShowTooltip() {
      const bool = this.$refs.content.offsetWidth < this.$refs.content.parentNode.offsetWidth;
      this.tooltipShow = bool;
    },
    goDetails(value) {
      let params = {
        bulkPlanID: value,
        mode: 'taskPlan',
        type: 'details',
      };
      this.openNewWindow({
        path: '/mainPage/identifierCenter/organizationInfo/till/bulkDetails',
        query: { params: encodeURIComponent(JSON.stringify(params)) },
      });
    },
  },
};
</script>

<style scoped></style>
