<script>
import OrgInfo from '@/views/identifier-center/pages/organization/detail/index.vue';
import AuditLog from '@/views/identifier-center/pages/organization-operator/detail/audit-log/index.vue';
import BalanceOverview from '@/views/identifier-center/pages/organization/detail/balanceoverview/index.vue';
import BankAccount from '@/views/identifier-center/pages/bank-account/list/index.vue';
import OrgChildren from '@/views/identifier-center/pages/organization/children/index.vue';
import OrgOperator from '@/views/identifier-center/pages/organization-operator/index.vue';
import OrgTill from '@/views/identifier-center/pages/till/list/index.vue';
import ReviewTransaction from '@/views/identifier-center/pages/review-transaction/list/index.vue';
import ReviewReversalRequest from '@/views/identifier-center/pages/review-reversal-request/list/index.vue';
import detailPage from '@/mixins/detail-page.vue';
import AccountStatement from '@/views/identifier-center/pages/account-statement/list/index.vue';
import TransactionInitiate from '@/views/identifier-center/pages/transaction-initiate/list/index.vue';
import multItem from './mult-item.vue';

export default {
  name: 'groupDetail',
  components: {
    OrgInfo,
    AuditLog,
    BalanceOverview,
    BankAccount,
    OrgChildren,
    OrgOperator,
    OrgTill,
    ReviewTransaction,
    ReviewReversalRequest,
    AccountStatement,
    TransactionInitiate,
    multItem,
  },
  provide() {
    return {
      detailRoot: this,
    };
  },
  mixins: [detailPage],
  data() {
    return {
      activeName: 'multItem', // label
      curComponentName: 'mult-item', //  component
      menuList: [
        {
          name: 'Overview', // list
          subMenu: [
            {
              name: 'Organization Info',
              label: 'organizationInfo',
              component: 'org-info',
            },
            {
              name: 'Audit Log',
              label: 'auditLog',
              component: 'audit-log',
            },
            {
              name: 'Bank Account',
              label: 'bankAccount',
              component: 'bank-account',
            },
            {
              name: 'Balance Overview',
              label: 'balance',
              component: 'balance-overview',
            },
          ],
        },
        {
          name: 'Identity',
          subMenu: [
            {
              name: 'Children',
              label: 'children',
              component: 'org-children',
            },
            {
              name: 'Operator',
              label: 'operator',
              component: 'org-operator',
            },
            {
              name: 'Till',
              label: 'till',
              component: 'org-till',
            },
          ],
        },
        {
          name: 'Transaction',
          subMenu: [
            {
              name: 'Initiate Transaction',
              label: 'initiateTransaction',
              component: 'transaction-initiate',
            },
            {
              name: 'Account Statement',
              label: 'accountStatement',
              component: 'account-statement',
            },
            {
              name: 'Review Transaction',
              label: 'reviewTransaction',
              component: 'review-transaction',
            },
            {
              name: 'Review Reversal Request',
              label: 'reviewReversalRequest',
              component: 'review-reversal-request',
            },
          ],
        },
      ],
    };
  },

  methods: {
    findActiveMenu(label) {
      const subMenuFlat = this.menuList.reduce((pre, next) => {
        if (!next.subMenu || !next.subMenu.length) {
          return pre;
        }
        return pre.concat(next.subMenu);
      }, []);
      return subMenuFlat.find((item) => item.label === label);
    },
    setCurComponentName(activeName) {
      const activeMenu = this.findActiveMenu(activeName);
      if (activeMenu) {
        this.handleChange(activeMenu);
      }
    },

    handleChange(activeMenu) {
      let { label, component } = activeMenu;

      this.activeMenu = activeMenu;
      this.curComponentName = component;
      if (label) {
        this.activeName = label;
        if (!this.params) {
          this.params = {};
        }
        switch (label) {
          case 'operator':
            this.params.activeName = 'organization';
            break;
          case 'till':
            this.params.activeName = true;
            break;
          case 'auditLog':
            this.params.initiatorType = 'Organization';
            this.params.hasSplitline = true;
            break;
        }
      }
    },
    gotoOrgDetailPage({ activeName, params = {} }) {
      if (!activeName) {
        activeName = 'organizationInfo';
      }

      const activeMenu = this.findActiveMenu(activeName);

      if (activeMenu) {
        this.params = params;
        this.handleChange(activeMenu);
      }
    },
  },
};
</script>
