<template>
  <div class="role-config business-portal-page" :class="{ 'has-split-line': isShowLine }">
    <div style="margin-bottom: 20px">
      <portal-header :projectObj="titleConfig" :showBottomLine="false" />
      <!--search -->
      <portal-form
        ref="searchList"
        :form-data="searchList"
        :form-config-list="formConfigList"
        :searchBtnLoading="searchLoading"
        @common-enter-event="search"
        @reset-btn-click="reset"
        @search-btn-click="search"
      />
      <!-- columns -->
      <div class="table-list-container">
        <div style="padding-bottom: 16px">
          <el-dropdown @command="exportData">
            <el-button
              type="primary"
              size="small"
              :disabled="isExport || !hasPerm('IC_ORG_OPR_AUDITLOG_EXPORT_BTN')"
              v-preventReClick
            >
              {{ $t('common.button.export') }}
              <i class="el-icon-arrow-down el-icon--right" style="font-size: 12px"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="XLS">Excel</el-dropdown-item>
              <el-dropdown-item command="XLSX">Excel 2007</el-dropdown-item>
              <el-dropdown-item command="CSV">CSV</el-dropdown-item>
              <el-dropdown-item command="PDF">PDF</el-dropdown-item>
              <el-dropdown-item command="TXT">TXT</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        <table-page
          size="small"
          class="table-page"
          :has-pagination="hasPage"
          :columns="columns"
          :pagination="paginationConfig"
          :getTableData="getTableData"
          :is-search="refreshTableFlag"
          :sortArr="sortArr"
          :initializingLoading="false"
        >
          <template slot="logTime" slot-scope="scope">
            {{ scope.data.row.logTime | dateformat('DD-MM-YYYY HH:mm:ss') }}
          </template>
          <template slot="operation" slot-scope="scope">
            <el-button
              type="text"
              class="btn"
              @click="handleDetail(scope.data.row)"
              :disabled="!hasPerm('IC_ORG_OPR_AUDITLOG_VIEW_BTN')"
            >
              {{ $t('common.label.detail') }}
            </el-button>
          </template>
        </table-page>
        <DetailDialog ref="viewDetail"></DetailDialog>
      </div>
    </div>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage';
import PortalForm from '@/components/portal-form';
import PortalHeader from '@/components/portal-header';
import {
  getListAuditLog,
  queryEventGroup,
  queryEventType,
  getCategoryList,
  queryAuditLogDetail,
  downloadAuditLog,
  getLimitDay,
  listTakeCallReferenceAuditLog,
} from '@/views/identifier-center/api/api-audit-log';
import DetailDialog from '@/views/identifier-center/pages/till/view/components/detail-dialog';
import dayjs from 'dayjs';
import { dateFormat } from '@/common/utils/date-time-utils.js';
import { exportUtils } from '@/common/utils/export-utils';

export default {
  components: { TablePage, PortalForm, PortalHeader, DetailDialog },
  name: 'AuditLog',
  props: {
    identityId: {
      type: String,
      default: '',
    },
    initiatorType: {
      type: String,
      default: 'Organization Operator',
    },
    title: {
      type: Object,
      default: () => {
        return;
      },
    },
    hasSplitline: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isShowLine: false,
      isExport: true,
      hasPage: false,
      searchLoading: false,
      refreshTableFlag: false,
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      titleConfig: {
        title: this.$t('IDC.auditlog.AuditLogTitle'),
        desc: this.$t('IDC.auditlog.desc'),
      },
      searchList: {
        type: '',
        event: '',
        eventGroup: '',
        identityId: '',
        initiatorId: '',
        initiatorType: '',
        startTime: '',
        endTime: '',
        time: [],
      },
      categoryList: [],
      eventList: [
        {
          label: 'All',
          value: '',
        },
      ],
      eventGroupList: [
        {
          label: 'All',
          value: '',
        },
      ],
      columns: [
        {
          label: this.$t('IDC.till.initiatorType'),
        },
        {
          label: this.$t('IDC.till.initiator'),
        },
        {
          label: this.$t('IDC.till.times'),
        },
        {
          label: this.$t('IDC.till.event'),
        },
        {
          label: this.$t('IDC.auditlog.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          width: '100',
        },
      ],
      direction: 'rtl',
      choiceDate: '',
      limitDays: {},
      pickerOptions: {
        // 
        onPick: ({ maxDate, minDate }) => {
          if (minDate && !maxDate) {
            this.choiceDate = minDate.getTime();
          }
          if (maxDate) {
            this.choiceDate = '';
          }
        },
        disabledDate: (time) => {
          let todayEndTime = new Date(dateFormat(new Date().getTime(), 'YYYY-MM-DD') + ' 23:59:59').getTime();
          if (!this.isNull(this.choiceDate)) {
            const one = this.limitDays.maxDay * 24 * 3600 * 1000;
            let maxTime = this.choiceDate + one - 1;
            maxTime = maxTime > todayEndTime ? todayEndTime : maxTime;
            const minTime = this.choiceDate - one + 1;
            return time.getTime() < minTime || time.getTime() > maxTime;
          } else {
            return time.getTime() > todayEndTime;
          }
        },
      },
      sortArr: [],
      isReset: false,
      isHome: false,
      initiatorId: '',
    };
  },
  created() {
    this.getLimitDay();
    this.getCategoryList();
    let initiatorTypeList = ['Organization', 'Customer', 'Till', 'Group'];
    if (initiatorTypeList.includes(this.initiatorType)) {
      this.searchList.identityId = this.identityId;
    } else {
      let params = JSON.parse(decodeURIComponent(this.$route.query.data));
      if (params.type) {
        this.initiatorId = params.initiatorId;
        this.searchList.initiatorId = params.initiatorId;
        this.isHome = true;
      } else {
        this.searchList.identityId = JSON.parse(decodeURIComponent(this.$route.query.data)).orgOperatorId;
      }
      if (params.orgOperatorId) {
        this.isShowLine = true;
      }
      if (params.title) {
        this.titleConfig = params.title;
      }
      if (params.hasSplitline) {
        this.isShowLine = params.hasSplitline;
      }
    }
    if (this.title) {
      this.titleConfig = this.title;
    }
    if (this.hasSplitline) {
      this.isShowLine = this.hasSplitline;
    }
  },
  watch: {
    initiatorId() {
      this.searchList.initiatorId = this.initiatorId;
    },
    'searchList.type': {
      handler(value) {
        if (value != '') {
          this.getColumns(value);
          this.getGroupEventList(value);
        } else {
          this.searchList.eventGroup = '';
          this.searchList.event = '';
          this.eventList = [
            {
              label: 'All',
              value: '',
            },
          ];
        }
      },
    },
    'searchList.eventGroup': {
      handler(value) {
        if (value != '') {
          this.getEventList(value);
        } else {
          this.searchList.event = '';
          this.eventList = [
            {
              label: 'All',
              value: '',
            },
          ];
        }
      },
    },
  },
  computed: {
    formConfigList() {
      return [
        {
          label: this.$t('IDC.auditlog.category'),
          key: 'type',
          comp: 'el-select',
          options: this.categoryList,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
        },
        {
          label: this.$t('IDC.till.eventGroup'),
          key: 'eventGroup',
          comp: 'el-select',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            id: 'eventGroup',
          },
          options: this.eventGroupList,
        },
        {
          label: this.$t('IDC.auditlog.event'),
          key: 'event',
          comp: 'el-select',
          options: this.eventList,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
          hidden: false,
        },
        {
          formItemClass: 'form-special-date-box',
          sameGroup: true,
          label: this.$t('IDC.auditlog.time'),
          description: this.$t('IDC.auditlog.timeDesc', { obj: this.limitDays.maxDay }),
          prop: 'time',
          comp: [
            {
              key: 'time',
              comp: 'el-date-picker',
              bind: {
                align: 'right',
                type: 'datetimerange',
                format: 'dd/MM/yyyy HH:mm:ss',
                'range-separator': '-',
                'value-format': 'yyyy-MM-dd HH:mm:ss',
                'default-time': ['00:00:00', '23:59:59'],
                'start-placeholder': 'Please select',
                'end-placeholder': 'Please select',
                'picker-options': this.pickerOptions,
              },
            },
            {
              comp: 'i',
              class: 'el-icon-date data_icon',
            },
          ],
        },
      ];
    },
  },
  methods: {
    getColumns(value) {
      this.hasPage = true;
      this.columns = [];
      this.sortArr = [];
      this.categoryList.forEach((item) => {
        if (item.value === value) {
          item.tableHead.forEach((column) => {
            if (value === 'TASK') {
              let propsList = ['taskType', 'taskId', 'relatedWorkflowId'];
              if (propsList.includes(column.prop)) {
                column.prop = 'extraResult.' + column.prop;
              }
            }
            column['align'] = 'left';
            if (column.prop === 'logTime') {
              column['__slotName'] = 'logTime';
              column['sort-method'] = (a, b) => {
                const aTime = dayjs(a.logTime);
                const bTime = dayjs(b.logTime);
                return aTime - bTime;
              };
            }
            column['sort-method'] = (a, b) => {
              return a[column.prop] > b[column.prop] ? 1 : -1;
            };
            this.sortArr.push(column.prop);
            this.columns.push(column);
          });
        }
      });
      this.columns.push({
        label: this.$t('IDC.auditlog.operation'),
        prop: 'operation',
        align: 'left',
        __slotName: 'operation',
        width: '100',
      });
    },
    isNull(value) {
      return !value;
    },
    async getCategoryList() {
      let res = await getCategoryList();
      if (res.header && res.header.success) {
        this.categoryList = res.list || [];
      }
    },
    async getGroupEventList(value) {
      let param = {
        type: value,
      };
      this.searchList.eventGroup = '';
      this.searchList.event = '';
      let res = await queryEventGroup(param);
      if (res.header && res.header.success) {
        this.eventGroupList = res.list;
      }
    },
    async getLimitDay() {
      let res = await getLimitDay();
      if (res) {
        this.limitDays = res.map || { maxDay: 30, defaultDay: 7 };
        this.getdefaultTime(this.limitDays.defaultDay);
      }
    },
    getdefaultTime(time) {
      this.searchList.startTime = dateFormat(new Date().getTime() - 3600 * 1000 * 24 * time, 'YYYY-MM-DD HH:mm:ss');
      this.searchList.endTime = dateFormat(new Date().getTime(), 'YYYY-MM-DD HH:mm:ss');
      this.searchList.time = [this.searchList.startTime, this.searchList.endTime];
    },
    async getEventList(value) {
      let param = {
        type: this.searchList.type,
        eventGroup: value,
      };
      this.searchList.event = '';
      let res = await queryEventType(param);
      if (res.header && res.header.success) {
        this.eventList = res.list;
      }
    },
    reset() {
      this.getLimitDay();
      this.isExport = true;
      this.searchList = {
        type: '',
        event: '',
        eventGroup: '',
        identityId: this.searchList.identityId,
        initiatorId: this.searchList.initiatorId,
        initiatorType: '',
        startTime: '',
        endTime: '',
        time: [],
      };
      this.isReset = true;
      this.refreshTableFlag = true;
      this.paginationConfig = {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      };
      this.eventGroupList = [
        {
          label: 'All',
          value: '',
        },
      ];
      this.eventList = [
        {
          label: 'All',
          value: '',
        },
      ];
      this.columns = [
        {
          label: this.$t('IDC.till.initiatorType'),
        },
        {
          label: this.$t('IDC.till.initiator'),
        },
        {
          label: this.$t('IDC.till.times'),
        },
        {
          label: this.$t('IDC.till.event'),
        },
        {
          label: this.$t('IDC.auditlog.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          width: '100',
        },
      ];
    },
    search() {
      let res = this.getEmpty();
      if (res) {
        this.refreshTableFlag = true;
        this.paginationConfig.pageIndex = 1;
      }
    },
    getEmpty() {
      if (this.searchList.type === '') {
        this.$message({ message: 'The category cannot be empty.', type: 'warning' });
        return false;
      }
      if (!this.searchList.time || (this.searchList.time && this.searchList.time.length == 0)) {
        this.$message({ message: 'The time cannot be empty.', type: 'warning' });
        return false;
      }
      return true;
    },
    exportData(fileType) {
      let res = this.getEmpty();
      if (res) {
        let paramObj = Object.assign(this.searchList, {
          type: this.searchList.type,
          initiatorType: this.initiatorType,
          event: this.searchList.event,
          eventGroup: this.searchList.eventGroup,
          startTime: dayjs(this.searchList.time[0]).format('YYYY-MM-DD HH:mm:ss') || '2022-11-01 00:00:00',
          endTime: dayjs(this.searchList.time[1]).format('YYYY-MM-DD HH:mm:ss') || '2022-11-30 23:59:59',
          fileType: fileType,
        });
        if (this.isHome) {
          paramObj.initiatorId = this.searchList.initiatorId;
        } else {
          paramObj.identityId = this.searchList.identityId;
        }
        delete paramObj.initiatorType;
        exportUtils(downloadAuditLog, paramObj, { fileType });
      }
    },
    async handleDetail(val) {
      let param = {
        auditLogId: val.auditLogId,
        type: val.type,
        logTime: val.logTime,
      };
      let res = await queryAuditLogDetail(param);
      let data = {};
      if (res && res.header.success) {
        let result = Object.assign(res.commonDetail, res.extraDetail);
        if (val.event == 'Take a Call') {
          data = await listTakeCallReferenceAuditLog({
            startTime: this.searchList.startTime,
            endTime: this.searchList.endTime,
            type: val.type,
            referenceId: val.auditLogId,
          });
          data.list.map((item) => {
            item.time = item.logTime.slice(0, 10).split('-').join('');
            return item;
          });
          result.list = data.list;
        }
        result.initiatorType = this.initiatorType;
        result['eventResult'] = res.eventResult;
        this.$refs.viewDetail.showDetail(result);
      }
    },
    handleClose(done) {
      done();
    },
    getTableData() {
      if (this.isReset) {
        this.isReset = false;
        return new Promise((resolve) => {
          resolve({
            list: [],
            totalRecords: 0,
            header: {
              success: false,
            },
          });
          this.refreshTableFlag = false;
          this.searchLoading = false;
        });
      }
      let params = {
        type: this.searchList.type,
        event: this.searchList.event,
        eventGroup: this.searchList.eventGroup,
        startTime: this.searchList.time && this.searchList.time[0],
        endTime: this.searchList.time && this.searchList.time[1],
        pageNo: this.paginationConfig.pageIndex,
        pageSize: this.paginationConfig.pageSize,
      };
      if (this.isHome) {
        params.initiatorId = this.searchList.initiatorId;
      } else {
        params.identityId = this.searchList.identityId;
      }
      return new Promise((resolve) => {
        getListAuditLog(params)
          .then((res) => {
            if (res && res.header.success) {
              res.list = res.resultList || [];
              if (res.list.length > 0) {
                this.isExport = false;
              } else {
                this.isExport = true;
              }
              res.totalRecords = Number(res.totalAmount) || 0;
              this.refreshTableFlag = false;
              this.searchLoading = false;
              resolve(res);
            } else {
              res.list = [];
              res.totalRecords = 0;
              this.refreshTableFlag = false;
              this.searchLoading = false;
              resolve(res);
            }
          })
          .catch(() => {
            resolve();
          });
      });
    },
  },
};
</script>

<style scoped lang="scss">
/deep/ .el-drawer__body {
  padding-left: 20px;
}
/deep/.page-desc {
  font-size: 12px;
  color: $--text_color--secondary;
}
.has-split-line .page-header {
  border-bottom: 1px solid $--fill_color--regular !important;
}
.business-portal-page {
  background-color: $--global-background_color;
}
</style>
