<template>
  <div class="menu-detail">
    <el-scrollbar wrap-class="scrollbar-wrapper">
      <el-menu class="el-menu" :default-active="active" @select="handleSelect" :default-openeds="openeds">
        <el-submenu v-for="(itemP, indexP) in dataList" :key="indexP + ''" :index="indexP + ''">
          <template slot="title">
            <span>{{ itemP.name || itemP.label }}</span>
          </template>
          <el-menu-item v-for="(item, index) in dataList[indexP].subMenu" :key="index + ''" :index="item.label">
            <div :title="item[showKey1]">
              <div class="item_name item_num_active">{{ item[showKey1] || item.name }}</div>
            </div>
          </el-menu-item>
        </el-submenu>
      </el-menu>
    </el-scrollbar>
  </div>
</template>

<script>
export default {
  name: 'MenuList',
  props: {
    activeIndex: {
      type: String,
      default: '',
    },
    dataList: {
      type: Array,
      default: () => [],
    },
    showKeys: {
      type: Array,
      default: () => [],
    },
    type: {
      type: String,
      default: 'normal',
    },
  },
  data() {
    return {
      active: this.dataList[0][this.showKey1],
    };
  },
  computed: {
    showKey1() {
      return this.showKeys[0] || '';
    },
    showKey2() {
      return this.showKeys[1] || '';
    },
    showKey3() {
      return this.showKeys[2] || '';
    },
    openeds() {
      return this.dataList.map((item, index) => {
        return '' + index;
      });
    },
  },
  watch: {
    activeIndex: {
      handler(value) {
        this.active = value + '';
      },
      immediate: true,
    },
  },
  methods: {
    handleSelect(label, indexPath) {
      const { dataList } = this;
      const { subMenu } = dataList[indexPath[0]];
      let activeMenu = subMenu.filter((sf) => sf.label === label)[0] || {};
      this.$emit('update:activeIndex', label);
      this.$emit('list-change', activeMenu);
    },
  },
};
</script>

