<template>
  <el-table
    ref="tableRef"
    class="orgTable"
    empty-text="No records found."
    :header-cell-style="headerStyle"
    :data="outSideTableData"
    :size="size"
    :border="border"
    v-bind="$attrs"
    :row-key="idKey"
    :key="tableKey"
    :max-height="maxHeight"
  >
    <template v-for="(item, index) in columns">
      <el-table-column
        :sortable="sortFun(item)"
        :key="index"
        v-bind="item"
        :width="item.width || ''"
        :formatter="item.formatter || formatterHandle"
      >
        <template v-if="item.__slotName" v-slot="scope">
          <slot :name="item.__slotName" :data="scope"></slot>
        </template>
      </el-table-column>
    </template>
  </el-table>
</template>

<script>
export default {
  name: 'org-product-table',
  props: {
    // 
    columns: {
      type: Array,
      default: () => [],
    },
    // 
    size: {
      type: String,
      default: 'medium',
    },

    /**
     * 
     */
    idKey: {
      type: String,
      default: 'id',
    },
    // props sortArr,
    sortArr: {
      type: Array,
      default: () => [],
    },

    /**
     * 
     */
    pageParam: {
      type: Object,
      default: () => {
        return {
          pageOffset: 'pageNo',
          pageSize: 'pageSize',
        };
      },
    },
    // 
    hasPagination: {
      type: Boolean,
      default: true,
    },
    pagination: {
      type: Object,
      default: () => {
        return {
          total: 0,
          pageIndex: 1,
        };
      }, //  === pageSize:pageIndex:
    },
    // 
    pageSizes: {
      type: Array,
      default() {
        return [10, 20, 50, 100];
      },
    },

    outSideTableData: {
      type: Array,
      default: () => [],
    },

    border: {
      // 
      type: Boolean,
      default: true,
    },
    initChecks: {
      type: Array,
      default: () => [],
    },
    maxHeight: {
      type: [Number, String],
    },
  },

  data() {
    return {
      tableKey: new Date().getTime(),
      headerStyle: {
        'background-color': `${this.GlobalStyleVariables.fillLightColor}`,
        'font-size': '14px',
        'font-family': 'HuaweiSans-Medium',
        'font-weight': 500,
        color: `${this.GlobalStyleVariables.textEmphasisColor}`,
      },
    };
  },

  methods: {
    sortFun(item) {
      // 
      const sortable = this.sortArr && this.sortArr.indexOf(item.prop) > -1;
      if (sortable) {
        if (!item['sort-method']) {
          return 'custom';
        }
        return true;
      }
      return false;
    },

    /**
     * 
     */
    formatterHandle(row, column) {
      const property = column.property;
      let newRow = row;
      let propertyVal = '';
      let propertyArray = property && property.split('.');
      propertyArray.forEach((pf) => {
        if (typeof newRow[pf] === 'object') {
          newRow = newRow[pf];
        } else {
          propertyVal = newRow[pf];
        }
      });
      if (propertyVal !== '' && propertyVal !== null && propertyVal !== undefined) {
        return propertyVal;
      }
      return '-';
    },
  },
};
</script>
