<template>
  <div class="panel">
    <div v-if="showBillValid" class="panel-header">{{ $t('IDC.organizationDetail.productDesc') }}</div>
    <!-- Select Product -->
    <el-form label-width="100px" label-position="top" ref="productForm" :model="productObj" :rules="productFormRules">
      <div class="content-wrapper">
        <div class="sub-block">
          <el-row style="margin: 20px 0" v-if="mode === 'edit' && !isEditModel">
            <el-col>
              <el-button size="small" type="primary" plain @click="productBtnClickHandle">
                <i class="el-icon-edit el-icon--right"></i>
                {{ $t('common.button.edit') }}
              </el-button>
            </el-col>
          </el-row>
          <div class="sub-block__title sub-block__title--normal">{{ $t('IDC.organization.product') }}</div>
          <el-row style="margin: 20px 0" v-if="isEditModel">
            <el-col>
              <el-button size="small" plain type="primary" icon="el-icon-plus" @click="productBtnClickHandle">{{
                $t('common.button.add')
              }}</el-button>
            </el-col>
          </el-row>
          <!---- productList----->
          <div class="product-table-container">
            <org-product-table
              class="product-table"
              ref="producuntRef"
              :columns="productColumnsConfig"
              :out-side-table-data="getAllProduct"
              v-bind="getProductStyleOptions"
            >
              <!-- effectiveDate-->
              <template slot="effectiveTime" slot-scope="scope">
                <span
                  v-if="!isEditModel || (!scope.data.row.isEditModel && !scope.data.row.isNewItem)"
                  v-text="formatDisplayDate(scope.data.row.effectiveTime)"
                ></span>
                <el-form-item
                  v-else
                  :prop="
                    scope.data.$index < productObj.productList.length
                      ? 'productList.' + scope.data.$index + '.effectiveTime'
                      : 'selectedProduct.' + (scope.data.$index - productObj.productList.length) + '.effectiveTime'
                  "
                  :rules="rules.dateRule(scope.data.row, 'effectiveTime')"
                >
                  <el-tooltip
                    effect="light"
                    placement="top-start"
                    popper-class="product-datepicker-tooltip"
                    :arrow-offset="40"
                    :content="scope.data.row.effectiveErrorMessage"
                    :disabled="!scope.data.row.effectiveErrorMessage"
                  >
                    <el-date-picker
                      v-model="scope.data.row.effectiveTime"
                      :value-format="DATE_FORMAT.COMP_VALUE"
                      :format="DATE_FORMAT.COMP_DISPLAY"
                      type="datetime"
                      size="small"
                      :placeholder="$t('common.placeholder.enter')"
                      :picker-options="pickerProdTimeStart(scope.data.row)"
                      @change="onDatePickerChange(scope, 'effectiveTime')"
                      :disabled="checkEffectived(scope.data.row)"
                      style="width: 100%"
                    ></el-date-picker>
                  </el-tooltip>
                </el-form-item>
              </template>

              <!-- expiredTime-->
              <template slot="expireTime" slot-scope="scope">
                <span
                  v-if="!isEditModel || (!scope.data.row.isEditModel && !scope.data.row.isNewItem)"
                  v-text="formatDisplayDate(scope.data.row.expireTime)"
                ></span>
                <el-form-item
                  v-else
                  :prop="
                    scope.data.$index < productObj.productList.length
                      ? 'productList.' + scope.data.$index + '.expireTime'
                      : 'selectedProduct.' + (scope.data.$index - productObj.productList.length) + '.expireTime'
                  "
                  :rules="rules.dateRule(scope.data.row, 'expireTime')"
                >
                  <el-tooltip
                    effect="light"
                    placement="top-start"
                    popper-class="product-datepicker-tooltip"
                    :arrow-offset="40"
                    :content="scope.data.row.expireErrorMessage"
                    :disabled="!scope.data.row.expireErrorMessage"
                  >
                    <el-date-picker
                      v-model="scope.data.row.expireTime"
                      :value-format="DATE_FORMAT.COMP_VALUE"
                      :format="DATE_FORMAT.COMP_DISPLAY"
                      type="datetime"
                      size="small"
                      @change="onDatePickerChange(scope, 'expireTime')"
                      :placeholder="$t('common.placeholder.enter')"
                      :picker-options="pickerProdTimeEnd(scope.data.row)"
                      style="width: 100%"
                    ></el-date-picker>
                  </el-tooltip>
                </el-form-item>
              </template>

              <!--  status-->
              <template slot="status" slot-scope="scope">
                <div
                  class="col-status"
                  v-if="!isEditModel || (!scope.data.row.isEditModel && !scope.data.row.isNewItem)"
                >
                  <svg-icon
                    icon-class="circle"
                    class="status status-active"
                    v-if="scope.data.row.productStatus === PRODUCT_STATUS.ACTIVE.code"
                  ></svg-icon>
                  <svg-icon
                    icon-class="circle"
                    class="status status-deaf"
                    v-else-if="scope.data.row.productStatus === PRODUCT_STATUS.SUSPENDED.code"
                  ></svg-icon>
                  <svg-icon
                    icon-class="circle"
                    class="status status-inactive"
                    v-else-if="scope.data.row.productStatus === PRODUCT_STATUS.CLOSED.code"
                  ></svg-icon>
                  {{ showProductStatus(scope.data.row.productStatus) }}
                </div>
                <el-select v-else v-model="scope.data.row.productStatus" size="small">
                  <el-option
                    v-for="(item, index) in productStatusList"
                    :key="index"
                    :label="item.name"
                    :value="item.code"
                  ></el-option>
                </el-select>
              </template>

              <template slot="chargeProfile" slot-scope="scope">
                <span
                  v-if="!isEditModel || (!scope.data.row.isEditModel && !scope.data.row.isNewItem)"
                  v-text="getChargeProfileLabelById(scope.data.row.chargeProfiles, scope.data.row.chargeProfileId)"
                ></span>
                <el-select
                  v-else
                  v-model="scope.data.row.chargeProfileId"
                  :placeholder="$t('common.placeholder.select')"
                  size="small"
                >
                  <el-option
                    v-for="(item, index) in scope.data.row.chargeProfiles"
                    :key="index"
                    :label="item.chargeProfileName"
                    :value="item.chargeProfileId"
                  ></el-option>
                </el-select>
              </template>

              <template slot="operation" slot-scope="scope">
                <el-button
                  type="text"
                  size="small"
                  v-if="scope.data.row.isNewItem"
                  @click="() => deletNewProductFromSelected(scope.data.row)"
                  >Delete</el-button
                >
                <el-button
                  type="text"
                  size="small"
                  v-else-if="scope.data.row.isEditModel"
                  @click="() => cancelCurrentRowOperation(scope.data.row)"
                  >Cancel</el-button
                >
                <el-button
                  v-else-if="!scope.data.row.isEditModel"
                  type="text"
                  size="small"
                  @click="() => setRowOperationModel(scope.data.row, true)"
                  >Edit</el-button
                >
              </template>
              <template slot="productName" slot-scope="scope">
                <span v-text="scope.data.row.productName"></span>
              </template>
            </org-product-table>
          </div>
        </div>

        <div v-if="showBillReferenceNumberValidation">
          <div class="title">Bill Reference Number Validation</div>
          <!-- validationMode -->
          <el-row :gutter="20">
            <el-col :span="8">
              <el-form-item label="Validation Mode" prop="payBillValidateForm.validationMode">
                <span v-text="getValidationMode(productObj.payBillValidateForm.validationMode)" v-if="isViewMode" />
                <el-select
                  v-else
                  v-model="productObj.payBillValidateForm.validationMode"
                  :placeholder="$t('common.placeholder.select')"
                  size="small"
                >
                  <el-option
                    v-for="(item, index) in validationModeOptions"
                    :value="item.value"
                    :label="item.label"
                    :key="index"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8" v-if="showRegularExpression">
              <el-form-item label="Regular Expression" prop="payBillValidateForm.validationExpression">
                <span v-if="isViewMode" v-text="productObj.payBillValidateForm.validationExpression" />
                <el-input
                  v-model="productObj.payBillValidateForm.validationExpression"
                  :placeholder="$t('common.placeholder.enter')"
                  size="small"
                  v-else
                />
              </el-form-item>
            </el-col>
          </el-row>

          <!-- allowPartialPayement -->
          <el-row :gutter="20">
            <el-col :span="8">
              <el-form-item label="Allow Partial Bill Payment" prop="payBillValidateForm.allowPartialPayement">
                <span
                  v-text="productObj.payBillValidateForm.allowPartialPayement === '1' ? 'YES' : 'NO'"
                  v-if="isViewMode"
                />
                <div v-else>
                  <el-radio v-model="productObj.payBillValidateForm.allowPartialPayement" label="1" size="small"
                    >YES</el-radio
                  >
                  <el-radio v-model="productObj.payBillValidateForm.allowPartialPayement" label="0" size="small"
                    >NO</el-radio
                  >
                </div>
              </el-form-item>
            </el-col>
          </el-row>
        </div>

        <!---- Remark---->
        <div style="margin-top: 20px">
          <el-form-item :label="$t('IDC.groupDetail.label.Remark')" prop="remark" v-if="isEditModel">
            <el-input :rows="4" type="textarea" v-model="productObj.remark" placeholder="Please enter"></el-input>
          </el-form-item>
        </div>
      </div>
    </el-form>
    <div class="flex flex-jus-end" v-if="isEditModel">
      <el-button id="cancel" class="cancel-button" size="small" @click="resetEvn">Cancel </el-button>
      <el-button
        id="submitProduct"
        type="primary"
        size="small"
        :loading="submitLoading"
        @click="submitEvn"
        v-preventReClick
      >
        Submit
      </el-button>
    </div>
    <product-dialog
      :productVisible.sync="showProductPopup"
      :addForm="popupConf"
      :isCheck="true"
      :isCheckMemory="true"
      :hasPagination="false"
      :childSearch="getProduct"
      @submit-call-back="addProduct"
    ></product-dialog>
  </div>
</template>
<script>
import productDialog from '@/views/identifier-center/pages/customer/create/components/product-dialog';
import OrgProductTable from '@/views/identifier-center/pages/organization/detail/components/org-product-table';
import {
  changeOrgProduct,
  queryAddProducts,
  querySubscribeProducts,
} from '@/views/identifier-center/api/api-organization';

import {
  dealApiRes,
  deepClone,
  getItemFromListByField,
  getLabelFromOptions,
} from '@/views/identifier-center/pages/organization/utils/common';
import { dateFormat } from '@/common/utils/date-time-utils';
import { DATE_FORMAT, getCurRowProductDateRules } from '@/views/identifier-center/validator/organization';
import dayjs from 'dayjs';
import { inputRequiredValidateObj, selectRequiredValidateObj } from '@/common/validator/common';

const PRODUCT_STATUS = {
  ACTIVE: {
    code: '01',
    name: 'Active',
  },
  SUSPENDED: {
    code: '02',
    name: 'Suspended',
  },
  CLOSED: {
    code: '03',
    name: 'Closed',
  },
};

const PRODUCT_DATE_ORIGINFORMAT = 'YYYY-MM-DD HH:mm';

export default {
  name: 'ProductInfo',
  components: { OrgProductTable, productDialog },
  props: {
    identityId: {
      type: String,
      required: true,
    },
    mode: {
      type: String,
      default: 'edit',
      enum: ['edit', 'review'],
    },
    showBillValid: {
      // productBillValid
      type: Boolean,
      default: true,
    },
    isReceiveData: {
      type: Boolean,
      default: false,
    },
    receiveProductData: {
      type: Array,
      default: () => [],
    },
  },
  inject: ['orgDetailView'],
  data() {
    return {
      DATE_FORMAT,
      PRODUCT_STATUS,
      isEditModel: false,

      submitLoading: false,
      productObj: {
        remark: '',
        productList: [],
        selectedProduct: [],
        time: {
          unit: null,
          value: null,
        },
        payBillValidateForm: {
          validationMode: '',
          allowPartialPayement: '',
          validationExpression: '',
        },
      },
      validationModeOptions: [
        {
          label: 'External Validation',
          value: '2',
        },
        {
          label: 'Internal and External',
          value: '3',
        },
        {
          label: 'Internal Validation',
          value: '1',
        },
        {
          label: 'No Validation',
          value: '0',
        },
      ],
      closedProducts: [],
      closedProductColumns: [
        { label: this.$t('IDC.organization.table.productName'), prop: 'productName', align: 'left' },
        { label: this.$t('IDC.organization.table.effectiveDate'), prop: 'effectiveTime', align: 'left' },
        { label: this.$t('IDC.organization.table.expiryDate'), prop: 'expireTime', align: 'left' },
        {
          label: this.$t('IDC.organization.table.chargeProfile'),
          prop: 'chargeProfile',
          align: 'left',
          __slotName: 'chargeProfile',
        },
        { label: this.$t('common.label.description'), prop: 'productDesc', align: 'left' },
        { label: this.$t('common.label.status'), prop: 'status', align: 'left', __slotName: 'status' },
      ],
      showProductPopup: false,
      popupConf: {
        idKey: 'productId',
        title: 'Add Product',
        label: 'Product Name',
        columns: [
          { label: 'Product Name', prop: 'productName', align: 'left' },
          { label: 'Description', prop: 'productDesc', align: 'left' },
        ],
        searchForm: {
          formData: {
            name: null,
          },
          formConf: [
            {
              label: 'Product Name',
              key: 'name',
              prop: 'name',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
          ],
        },
      },

      rules: {
        dateRule: (row, target) => getCurRowProductDateRules(this, row, target),
      },
      payBillCheckRule: {},
    };
  },
  computed: {
    productColumnsConfig() {
      let productNameColumnNoraml = {
        label: this.$t('IDC.organization.table.productName'),
        prop: 'productName',
        align: 'left',
        'min-width': 200,
      };
      let productNameColumnFixed = {
        label: this.$t('IDC.organization.table.productName'),
        prop: 'productName',
        align: 'left',
        width: 200,
        fixed: 'left',
        __slotName: 'productName',
      };

      const columns = [
        {
          label: this.$t('IDC.organization.businessOperationsGroup'),
          prop: 'businessOperationsGroup',
          align: 'left',
          'min-width': 250,
        },
        {
          label: this.$t('IDC.organization.table.effectiveDate'),
          prop: 'effectiveTime',
          align: 'left',
          __slotName: 'effectiveTime',
          'min-width': 220,
        },
        {
          label: this.$t('IDC.organization.table.expiryDate'),
          prop: 'expireTime',
          align: 'left',
          __slotName: 'expireTime',
          'min-width': 220,
        },
        {
          label: this.$t('IDC.organization.chargeProfile'),
          prop: 'chargeProfile',
          align: 'left',
          __slotName: 'chargeProfile',
          'min-width': 200,
        },
        { label: this.$t('common.label.description'), prop: 'productDesc', align: 'left', 'min-width': 200 },
        {
          label: this.$t('common.label.status'),
          prop: 'status',
          align: 'left',
          __slotName: 'status',
          'min-width': 150,
        },
      ];
      if (this.isEditModel) {
        columns.unshift(productNameColumnNoraml);
        columns.push({
          label: this.$t('common.label.operation'),
          'min-width': 100,
          align: 'left',
          __slotName: 'operation',
          fixed: 'right',
        });
        return columns;
      } else {
        columns.unshift(productNameColumnFixed);
        return columns;
      }
    },

    getAllProduct() {
      return [].concat(this.productObj.productList, this.productObj.selectedProduct);
    },

    getProductStyleOptions() {
      if (this.getAllProduct.length >= 10) {
        return {
          height: '520',
        };
      }
      return {};
    },

    productStatusList() {
      const statusList = Object.values(this.PRODUCT_STATUS);
      return statusList;
    },

    showBillReferenceNumberValidation() {
      const productList = this.getAllProduct || [];
      return (
        productList.some((item) => {
          if (item.productProperties) {
            return item.productProperties.BILL_REF_NUM_RULE != undefined;
          }
        }) && this.showBillValid
      );
    },
    showRegularExpression() {
      const payBillValidateForm = this.productObj.payBillValidateForm;
      return (
        payBillValidateForm.validationMode &&
        payBillValidateForm.validationMode !== '0' &&
        payBillValidateForm.validationMode !== '2'
      );
    },
    isViewMode() {
      return !this.isEditModel;
    },
    productFormRules() {
      return {
        payBillValidateForm: this.isViewMode
          ? {}
          : {
              validationExpression: [inputRequiredValidateObj],
              validationMode: [selectRequiredValidateObj],
              allowPartialPayement: [selectRequiredValidateObj],
            },
        remark: [inputRequiredValidateObj],
      };
    },
  },

  created() {
    if (this.isReceiveData) {
      return;
    }
    this.init();
  },
  watch: {
    'orgDetailView.payBillCheckRule': {
      handler(val) {
        this.payBillCheckRule = { ...val };
        this.productObj.payBillValidateForm = { ...val };
      },
      deep: true,
      immediate: true,
    },
    receiveProductData: {
      handler(value) {
        this.productObj.productList = deepClone(value).map((item) => {
          const effectiveDate = item.effectiveDate.replace('T', ' ');
          const expireDate = item.expireDate.replace('T', ' ');
          const effectiveTime = dateFormat(effectiveDate, DATE_FORMAT.DAYJS, PRODUCT_DATE_ORIGINFORMAT);
          const expireTime = dateFormat(expireDate, DATE_FORMAT.DAYJS, PRODUCT_DATE_ORIGINFORMAT);
          item.effectiveTime = effectiveTime;
          item.expireTime = expireTime;
          // tool tip error init
          item.effectiveErrorMessage = '';
          item.expireErrorMessage = '';
          item.chargeProfiles = item.chargeProfiles || [];

          item.originRow = deepClone(item);
          return item;
        });
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    init() {
      this.initProduct();
    },

    async initProduct() {
      const params = {
        identityId: this.identityId,
      };
      let products = await dealApiRes(querySubscribeProducts, 'list', params);
      products = deepClone(products);
      this.productObj.productList.length = 0;
      this.productObj.selectedProduct.length = 0;
      if (!products) {
        return;
      }
      products = products.map((item) => {
        // format date time
        const effectiveDate = item.effectiveDate.replace('T', ' ');
        const expireDate = item.expireDate.replace('T', ' ');
        const effectiveTime = dateFormat(effectiveDate, DATE_FORMAT.DAYJS, PRODUCT_DATE_ORIGINFORMAT);
        const expireTime = dateFormat(expireDate, DATE_FORMAT.DAYJS, PRODUCT_DATE_ORIGINFORMAT);
        item.effectiveTime = effectiveTime;
        item.expireTime = expireTime;
        // tool tip error init
        item.effectiveErrorMessage = '';
        item.expireErrorMessage = '';

        item.originRow = deepClone(item);
        return item;
      });

      this.productObj.productList.push(...products);
    },
    async initClosedProduct() {
      const params = {
        identityId: this.identityId,
      };
      // closed
      let products = [];

      if (!products) {
        return;
      }
      products = products.map((item) => {
        // format date time
        const effectiveDate = item.effectiveDate.replace('T', ' ');
        const expireDate = item.expireDate.replace('T', ' ');

        const effectiveTime = dateFormat(effectiveDate, DATE_FORMAT.DAYJS, PRODUCT_DATE_ORIGINFORMAT);
        const expireTime = dateFormat(expireDate, DATE_FORMAT.DAYJS, PRODUCT_DATE_ORIGINFORMAT);
        item.effectiveTime = effectiveTime;
        item.expireTime = expireTime;
        return item;
      });
      this.closedProducts = products;
    },
    refreshProuduct() {
      this.initProduct();
    },
    showProductStatus(code) {
      const statusTypes = Object.values(PRODUCT_STATUS);
      const curStatusObj = getItemFromListByField(statusTypes, code, 'code') || {};
      return curStatusObj.name || '-';
    },
    getChargeProfileLabelById(list, id) {
      const chargeProfile = getItemFromListByField(list, id, 'chargeProfileId') || {};
      return chargeProfile.chargeProfileName || '-';
    },
    productBtnClickHandle() {
      if (!this.isEditModel) {
        this.changeEditModel(true);
      } else {
        this.showProductPopup = true;
      }
    },
    async getProduct() {
      let res = {
        header: {
          success: false,
        },
        list: [],
      };

      this.prodLoading = true;
      try {
        const params = {
          identityId: this.identityId,
        };
        const list = await dealApiRes(queryAddProducts, 'list', params);

        res.list = list;
        res.header.success = true;
      } catch (error) {}
      this.prodLoading = false;

      res.list = res.list.filter(
        (item) => !this.getAllProduct.find((selected) => selected.productId === item.productId)
      );

      return res;
    },
    resetEvn() {
      this.$confirm('Are you sure to cancel the operation?', {
        confirmButtonText: 'Confirm',
        cancelButtonText: 'Cancel',
        closeOnClickModal: false,
        type: 'warning',
      }).then(() => {
        this.refreshProuduct();
        this.changeEditModel(false);
        this.productObj.payBillValidateForm = { ...this.payBillCheckRule };
        this.$emit('product-cancel');
      });
    },
    submitEvn() {
      this.$refs.productForm.validate((valid) => {
        if (!valid) {
          return;
        }
        const products = this.getAllProduct.map((item) => {
          return {
            productId: item.productId,
            effectiveTime: item.effectiveTime,
            expireTime: item.expireTime,
            chargeProfileId: item.chargeProfileId,
            status: item.productStatus,
          };
        });
        if (this.isReceiveData) {
          const params = {
            products,
            remark: this.productObj.remark,
          };
          this.$emit('product-submit', params);
          return;
        }
        this.submitLoading = true;
        const payBillValidateForm = this.productObj.payBillValidateForm;
        const params = {
          newOrganizationProducts: products,
          receiverIdentityId: this.identityId,
        };
        if (this.showBillReferenceNumberValidation) {
          params.payBillCheckRule = {
            validationMode: payBillValidateForm.validationMode,
            validationExpression: payBillValidateForm.validationExpression,
            allowPartialPayement: payBillValidateForm.allowPartialPayement,
            notifyText: payBillValidateForm.notifyText,
          };
        }
        changeOrgProduct(params)
          .then((res) => {
            if (res.header && res.header.success) {
              if (res.taskId) {
                this.$message.success('The request must be approved by another operator.');
              } else {
                this.$message.success('Submit Successful');
              }
              this.refreshProuduct();
              this.changeEditModel(false);
              if (this.showBillReferenceNumberValidation) {
                this.payBillCheckRule = { ...payBillValidateForm };
              }
            }
          })
          .finally(() => {
            this.submitLoading = false;
          });
      });
    },
    changeEditModel(val) {
      this.isEditModel = val;
    },
    setRowOperationModel(row, model) {
      if (!row || typeof model === 'undefined') {
        return;
      }
      this.$set(row, 'isEditModel', model);
    },
    cancelCurrentRowOperation(row) {
      if (!row) {
        return;
      }
      const originRow = row.originRow;
      if (originRow) {
        row.effectiveTime = originRow.effectiveTime;
        row.expireTime = originRow.expireTime;
        row.chargeProfile = originRow.chargeProfile;
        row.productStatus = originRow.productStatus;
      }
      this.setRowOperationModel(row, false);
    },
    deletNewProductFromSelected(row) {
      if (!row) {
        return;
      }
      this.$modal
        .confirm('Are you sure to delete it?', 'Delete', {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancel',
          closeOnClickModal: false,
          type: 'warning',
        })
        .then(() => {
          let idx = this.productObj.selectedProduct.findIndex((item) => item === row);
          if (idx != -1) {
            this.productObj.selectedProduct.splice(idx, 1);
          }
        });
    },
    addProduct(rows) {
      let selectedProduct = deepClone(this.productObj.selectedProduct);
      let list = deepClone(rows);
      list = list.map((item) => {
        const selected = selectedProduct.find((s) => s.productId === item.productId);
        if (selected) {
          item.isEditModel = selected.isEditModel;
        } else {
          item.isEditModel = false;
        }

        const effectiveTime = dateFormat(item.effectiveDate, DATE_FORMAT.DAYJS, PRODUCT_DATE_ORIGINFORMAT);
        const expireTime = dateFormat(item.expireDate, DATE_FORMAT.DAYJS, PRODUCT_DATE_ORIGINFORMAT);
        item.effectiveTime = effectiveTime;
        item.expireTime = expireTime;
        item.isNewItem = true;
        //   active 
        item.productStatus = PRODUCT_STATUS.ACTIVE.code;

        item.effectiveErrorMessage = '';
        item.expireErrorMessage = '';

        item.originRow = deepClone(item);
        return item;
      });
      this.productObj.selectedProduct.push(...list);
    },
    getOptionsLabelByValue(list, val) {
      const label = getLabelFromOptions(list, val);
      return label || '-';
    },
    formatDisplayDate(date) {
      const display = dateFormat(date, DATE_FORMAT.DAYJS_DISPLAY, PRODUCT_DATE_ORIGINFORMAT);
      return display;
    },
    validationModeChange(val) {
      if (!val || val === 'No Validation') {
        this.regularExpression = '';
      }
    },
    pickerProdTimeStart(row = {}) {
      return {
        disabledDate(time) {
          // 
          // 
          //  >  > 
          const isLessNow = time.getTime() <= Date.now();

          const effectiveTime = dayjs(row.effectiveTime, DATE_FORMAT.DAYJS);
          const isEffectived = Date.now() > new Date(effectiveTime);

          if (isLessNow || isEffectived) {
            return true;
          }
          return time.getTime() < Date.now();
        },
      };
    },
    pickerProdTimeEnd(row = {}) {
      return {
        disabledDate(time) {
          const isLessNow = time.getTime() < Date.now();
          return isLessNow;
        },
      };
    },
    checkEffectived(row = {}) {
      if (row.effectiveTime) {
        const effectiveTime = dayjs(row.effectiveTime, DATE_FORMAT.DAYJS);
        return Date.now() > new Date(effectiveTime);
      } else {
        return false;
      }
    },
    onDatePickerChange(scope, target) {
      let prop;
      if (target === 'effectiveTime') {
        prop = 'expireTime';
      } else {
        prop = 'effectiveTime';
      }

      const propPath =
        scope.data.$index < this.productObj.productList.length
          ? 'productList.' + scope.data.$index + '.' + prop
          : 'selectedProduct.' + (scope.data.$index - this.productObj.productList.length) + '.' + prop;

      this.$nextTick(() => {
        if (prop === 'effectiveTime') {
          scope.data.row.effectiveErrorMessage = '';
        } else {
          scope.data.row.expireErrorMessage = '';
        }
        this.$refs.productForm.clearValidate([propPath]);
      });
    },
    getValidationMode(val) {
      switch (val) {
        case '0':
          return 'No Validation';
        case '1':
          return 'Internal Validation';
        case '2':
          return 'External Validation';
        case '3':
          return 'Internal and External';
      }
    },
  },
};
</script>
