<template>
  <div class="group-detail-page">
    <div>
      <div class="panel">
        <div class="panel-left">
          <menu-list
            :data-list="menuList"
            :show-keys="['name']"
            :activeIndex="activeName"
            :useLink="false"
            @list-change="handleChange"
          />
          <div class="left-mask"></div>
        </div>
        <div class="panel-middle"></div>
        <div class="panel-right">
          <transition name="load-transform" mode="out-in">
            <component :is="curComponentName" v-bind="getOptions" />
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MenuList from '@/components/portal-menu-list';

export default {
  name: 'detailPage',
  components: {
    MenuList,
  },
  provide() {
    return {
      detailRoot: this,
    };
  },
  data() {
    return {
      activeName: '',
      curComponentName: '',
      params: {},
      //  $route.query.data   identityId organizationIdpropShortCode
      routeParams: {},
      menuList: [],
    };
  },
  computed: {
    // prop
    getOptions() {
      return Object.assign(this.routeParams, this.params);
    },
  },
  created() {},
  activated() {
    this.init();
  },
  methods: {
    init() {
      const params = this.getRouteParam() || {};
      this.params = params;
      this.routeParams = params;
      if (params.activeName) {
        this.activeName = params.activeName;
        this.setCurComponentName(this.activeName);
      }
    },

    setCurComponentName() {},

    /**
     * 
     */
    getRouteParam() {
      let params = this.$route.query.data;
      if (!params) {
        return null;
      }
      params = decodeURIComponent(params);
      if (!params) {
        return null;
      }
      return JSON.parse(params);
    },
  },
};
</script>

