      padding-right: 40px;
      position: fixed;
      z-index: 10;
      background-color: $--global-background_color;
      .left-mask {
        height: 100%;
        width: 34px;
        position: absolute;
        top: 0;
        left: -34px;
        background: $--global-background_color;
      }
    }
    .panel-middle {
      width: 0px;
      background-color: $--global-background_color;
    }
    .panel-right {
      width: $witdhRight;
      margin-left: $witdhLeft;
    }
  }

  /* load-transform */
  .load-transform-leave-active,
  .load-transform-enter-active {
    transition: all 0.5s;
  }

  .load-transform-enter {
    transform: translateX(-30px);
    opacity: 0;
  }

  .load-transform-leave-to {
    transform: translateX(30px);
    opacity: 0;
  }
}
</style>

