<template>
  <el-dialog
    :title="$t('IDC.till.viewDetails')"
    :visible="isShow"
    width="560px"
    class="dialog-container"
    :close-on-click-modal="false"
    labelClassName="till-view-detail"
    @close="closeDialog()"
  >
    <div>
      <el-descriptions :column="1" v-for="(item, index) in detailsData" :key="index">
        <el-descriptions-item :label="item.label" v-if="!item.hidden"
          ><descriptions-tooltip :isJump="item.isJump || false" :value="item.value | formatEmpty"></descriptions-tooltip
        ></el-descriptions-item>
      </el-descriptions>
    </div>
    <div class="take-call-list">
      <el-table
        :data="labelData.list"
        v-if="labelData.list"
        header-cell-class-name="table-header-class"
        cell-class-name="table-cell-class"
      >
        <el-table-column
          property="initiatorType"
          label="initiatorType"
          show-overflow-tooltip
          width="90"
        ></el-table-column>
        <el-table-column property="initiatorId" label="initiatorId" show-overflow-tooltip></el-table-column>
        <el-table-column property="time" label="time" show-overflow-tooltip width="70"></el-table-column>
        <el-table-column property="event" label="event" show-overflow-tooltip></el-table-column>
      </el-table>
    </div>
  </el-dialog>
</template>
<script>
import DescriptionsTooltip from './descriptions-tooltip.vue';
export default {
  name: 'detailDialog',
  components: { DescriptionsTooltip },
  props: {},
  data() {
    return {
      isShow: false,
      labelData: {},
      type: '',
      initiatorType: '',
    };
  },
  computed: {
    detailsData() {
      return [
        {
          label: this.$t('IDC.till.masterType'),
          key: 'masterType',
          value: '',
        },
        {
          label: this.$t('IDC.till.masterID'),
          key: 'masterId',
          value: '',
        },
        {
          hidden: false,
          label: this.$t('IDC.till.Initiator'),
          key: 'initiatorInfo',
          value: '',
        },
        {
          hidden: false,
          label: this.$t('IDC.till.times'),
          key: 'logTime',
          value: '',
        },
        {
          hidden: false,
          label: this.$t('IDC.till.event'),
          key: 'event',
          value: '',
        },
        {
          hidden: this.isShowTakItem('IDENTITY'),
          label: this.$t('IDC.till.beforeImage'),
          key: 'beforeImage',
          value: '',
        },
        {
          hidden: this.isShowTakItem('IDENTITY'),
          label: this.$t('IDC.till.afterImage'),
          key: 'afterImage',
          value: '',
        },
        {
          hidden: false,
          label: this.$t('IDC.till.startTime'),
          key: 'startTime',
          value: '',
        },
        {
          hidden: false,
          label: this.$t('IDC.till.endTime'),
          key: 'endTime',
          value: '',
        },
        {
          isJump: true,
          hidden: this.isShowTakItem(),
          label: this.$t('IDC.till.taskID'),
          key: 'taskId',
          value: '',
        },
        {
          hidden: this.isShowTakItem(),
          label: this.$t('IDC.till.taskType'),
          key: 'taskType',
          value: '',
        },
        {
          hidden: this.isShowTakItem(),
          label: this.$t('IDC.till.taskCategory'),
          key: 'taskCategory',
          value: '',
        },
        {
          hidden: this.isShowTakItem(),
          label: this.$t('IDC.till.workflowID'),
          key: 'relatedWorkflowId',
          value: '',
        },
        {
          hidden: this.isTaskHidden(),
          label: this.$t('IDC.till.searchCriteria'),
          key: 'searchCriteria',
          value: '',
        },
        {
          hidden: this.isShowItem(),
          label: this.$t('IDC.till.hasBeenDownloaded'),
          key: 'hasBeenDownloaded',
          value: '',
        },
        {
          hidden: this.isShowItem(),
          label: this.$t('IDC.till.isSensitive'),
          key: 'isSensitive',
          value: '',
        },
        {
          hidden: this.isShowItem(),
          label: this.$t('IDC.till.infoType'),
          key: 'infoType',
          value: '',
        },
        {
          hidden: false,
          label: this.$t('IDC.till.reason'),
          key: 'reason',
          value: '',
        },
        {
          hidden: this.isTaskHidden(),
          label: this.$t('IDC.till.errorCode'),
          key: 'errorCode',
          value: '',
        },
        {
          hidden: this.isTaskHidden(),
          label: this.$t('IDC.till.errorMessage'),
          key: 'errorMsg',
          value: '',
        },
        {
          hidden: false,
          label: this.$t('IDC.till.clientIP'),
          key: 'fromIp',
          value: '',
        },
        {
          hidden: false,
          label: this.$t('IDC.till.eventResult'),
          key: 'eventResult',
          value: '',
        },
      ];
    },
  },
  methods: {
    isShowItem() {
      switch (this.type) {
        case 'QUERY':
          return false;
        default:
          return true;
      }
    },
    isTaskHidden() {
      if (this.type === 'TASK') {
        return true;
      } else {
        return false;
      }
    },
    isShowTakItem(type) {
      if (this.type === 'TASK' || type === 'IDENTITY') {
        return false;
      } else {
        return true;
      }
    },
    showDetail(val) {
      this.labelData = val;
      this.initiatorType = val.initiatorType;
      this.type = val.type;
      this.isShow = true;
      this.formatDta();
    },
    formatDta() {
      let labelData = this.labelData;
      for (var data in labelData) {
        this.detailsData.forEach((item, index) => {
          if (item.key === data) {
            this.detailsData[index].value = labelData[data].toString();
          }
        });
      }
    },
    closeDialog() {
      this.isShow = false;
      for (let i = 0; i < this.detailsData.length; i++) {
        this.detailsData[i].value = '';
      }
    },
    dataType(val) {
      if (typeof val === 'boolean') {
        return val.toString();
      } else {
        return '-';
      }
    },
  },
};
</script>
