    position: relative;
  }
  .el-descriptions-item__content {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 290px;
    position: absolute;
    left: 158px;
    color: $--text_color--emphasis;
    font-size: 14px;
  }
}
